let poly = require("preact-cli/lib/lib/webpack/polyfills");

import habitat from "preact-habitat";

import Widget from "./components/hello-world";
import App from "./components/app";



let components = [
{
  component: Widget,
      selector: '.widget'
},
  {
    component: App,
    selector: 'body'
  }
];

const mountComponents = components => {
  components.map(({ component, selector, defaultProps, clean }) => {
    habitat(component).render({ selector, defaultProps, clean });
  });
};

mountComponents(components);


